.sf-alert {
  z-index: 200;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  overflow-y: auto;
  padding: 30px 0;
}
.sf-alert__content {
  overflow: hidden;
  width: 600px;
  margin: 0 auto;
  vertical-align: middle;
  background-color: white;
  box-shadow: 0px 15px 45px rgba(0, 0, 0, 0.33);
  position: relative;
}
.sf-alert__body {
  padding: 1em 2em;
  display: table;
  vertical-align: middle;
  padding-bottom: 0;
  min-height: 6em;
}
.sf-alert__message {
  display: table-cell;
  vertical-align: middle;
  word-break: break-all;
}
.sf-alert__footer {
  padding: 1em 2em;
  text-align: right;
}
.sf-theme-green .sf-alert__content {
  box-shadow: 0px 3px 20px 0px rgba(0, 0, 0, 0.42);
  border: 1px solid #c9c9c9;
}
.sf-theme-green .sf-alert__sure {
  line-height: 2.3em;
  white-space: nowrap;
  box-sizing: border-box;
  padding: 0 1em;
  background-color: #27ab8d;
  color: white;
  border: none;
  border-radius: 2px;
  cursor: pointer;
  -webkit-transition: 0.3s background-color;
  transition: 0.3s background-color;
}
.sf-theme-green .sf-alert__sure:hover,
.sf-theme-green .sf-alert__sure:focus {
  outline: none;
  background-color: #208e75;
}
.sf-theme-green .sf-alert__sure:active {
  outline: none;
  background-color: #27ab8d;
}
.sf-theme-green .sf-alert__footer {
  text-align: right;
}
.sf-theme-green .sf-alert__body,
.sf-theme-green .sf-alert__footer {
  padding: 1em 1.5em;
}
.sf-theme-green .sf-alert__body {
  padding-bottom: 0;
}
.sf-theme-green .sf-alert__message {
  line-height: 1.5;
}
label.sf-checkbox {
  position: relative;
  line-height: 2em;
}
input[type="checkbox"].sf-checkbox__input {
  font-size: 1.25em;
  margin-right: 0.25em;
  cursor: pointer;
  width: 1em;
  text-align: center;
}
.sf-theme-green .sf-checkbox__input {
  z-index: 2;
  position: absolute;
  opacity: 0;
}
.sf-theme-green .sf-checkbox__fake {
  position: relative;
}
.sf-theme-green .sf-checkbox__fake::before {
  content: '';
  display: inline-block;
  width: 1.2em;
  height: 1.2em;
  box-sizing: border-box;
  border: 1px solid #c9c9c9;
  vertical-align: text-bottom;
  border-radius: 2px;
  margin-right: 0.25em;
}
.sf-theme-green .sf-checkbox__fake::after {
  content: '';
  display: inline-block;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  width: 0.3em;
  height: 0.60em;
  border-bottom: 1px solid white;
  border-right: 1px solid white;
  position: absolute;
  left: 0.4em;
  top: 0.15em;
  visibility: hidden;
}
.sf-theme-green .sf-checkbox__input:focus + .sf-checkbox__fake::before {
  border-color: #27ab8d;
  border-width: 2px;
}
.sf-theme-green .sf-checkbox__input:checked + .sf-checkbox__fake::before {
  border-color: #27ab8d;
  background-color: #27ab8d;
}
.sf-theme-green .sf-checkbox__input:checked + .sf-checkbox__fake::after {
  visibility: visible;
}
.sf-theme-green .sf-checkbox__input[disabled] + .sf-checkbox__fake,
.sf-theme-green .sf-checkbox__input[disabled] {
  cursor: not-allowed;
}
.sf-theme-green .sf-checkbox__input[disabled] + .sf-checkbox__fake::before {
  border-color: #c9c9c9;
  background-color: #f6f5f5;
}
.sf-theme-green .sf-checkbox__input[disabled] + .sf-checkbox__fake::after {
  border-color: #272536;
}
