  background-color: #27A1CA;
}
.sf-theme-green .sf-switch__label {
  border: 2px solid #27ab8d;
  height: 28px;
  border-radius: 3px;
}
.sf-theme-green .sf-switch--checked .sf-switch__label .sf-switch__switch {
  right: 0px;
  background-color: white;
}
.sf-theme-green .sf-switch__switch {
  background: white;
  border: 2px solid #27ab8d;
  border-radius: 3px;
  width: 28px;
  right: calc(100% - 28px);
}
.sf-theme-green .sf-switch__inner:before {
  padding-right: 24px;
  content: attr(x-ontext) " ";
  background-color: #27ab8d;
  color: #FFFFFF;
}
.sf-theme-green .sf-switch__inner:after {
  padding-left: 24px;
  content: attr(x-offtext) " ";
  background-color: white;
  color: #27ab8d;
}
.sf-notification {
  z-index: 100;
  position: fixed;
  top: 0;
  right: 10px;
  height: 0;
  width: 250px;
}
.sf-notification__item {
  width: 250px;
  height: 70px;
  line-height: 1.25em;
  background-color: white;
  padding: 0 1.25em;
  padding-left: 60px;
  padding-top: 10px;
  padding-bottom: 10px;
  position: relative;
  margin: 0 auto;
  margin-right: 20px;
  margin-top: 0.5em;
  display: table;
}
.sf-notification__content {
  padding-right: 1.25em;
}
.sf-notification__close {
  position: absolute;
  right: 0;
  top: 0;
  cursor: pointer;
  width: 2em;
  text-align: center;
  color: #999;
  height: 2.5em;
  line-height: 2.5em;
}
.sf-notification__header {
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 30px;
  text-align: center;
  font-size: 1.2em;
  width: 60px;
  height: 100%;
  background: white;
}
.sf-notification__header i {
  line-height: 3em;
  font-size: 25px;
}
.sf-theme-green .sf-notification__item {
  color: #333;
  border-radius: 2px;
  box-shadow: 0px 3px 20px 0px rgba(0, 0, 0, 0.42);
}
.sf-theme-green .sf-notification__item--default .sf-notification__header {
  color: #27ab8d;
}
.sf-theme-green .sf-notification__item--success .sf-notification__header {
  color: #5cb85c;
}
.sf-theme-green .sf-notification__item--danger .sf-notification__header {
  color: #ec4758;
}
.sf-theme-green .sf-notification__item--warn .sf-notification__header {
  color: #efa659;
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
h1 span a:hover {
  text-decoration: none;
}
.sf-theme-green .sf-page-footer {
  background-color: #9E9E9E;
  color: #FFF;
}



